<template>
  <div>
    <el-carousel height="650px">
      <el-carousel-item v-for="(item, index) in caArr" :key="index">
        <img :src="item">
      </el-carousel-item>
    </el-carousel>
    <div class="main">
      <el-row>
        <el-col :span="14" :offset="5">
          <el-breadcrumb separator-class="el-icon-arrow-right" class="breadcrumb">
            <el-breadcrumb-item :to="{ path: '/index' }">首页</el-breadcrumb-item>
          </el-breadcrumb>
          <div class="search">
            <el-row>
              <el-col>
                <el-form :inline="true" class="search-form">
                  <el-row :gutter="10">
                    <el-col :span="12">
                      <el-form-item label="项目状态">
                        <el-select v-model="searchValue.is_claimed" @change="getDate" clearable placeholder="请选择">
                          <el-option label="已认捐" value="true"></el-option>
                          <el-option label="未认捐" value="false"></el-option>
                        </el-select>
                      </el-form-item>
                    </el-col>
                    <el-col :span="12" class="text-right">
                      <el-form-item>
                        <el-input placeholder="请输入内容" v-model="searchValue.title__icontains" class="input-with-select">
                          <el-button slot="append" icon="el-icon-search" @click="search"></el-button>
                        </el-input>
                      </el-form-item>
                    </el-col>
                  </el-row>
                </el-form>
              </el-col>
            </el-row>
          </div>
          <yd-layout>
            <yd-infinitescroll :callback="loadList" ref="dreamList">
              <div class="list" slot="list">
                <item v-for="(item, index) in list" :key="index" :data="item" @claim="claim" :receive="receive" :noReceive="noReceive"/>
              </div>
              <span slot="doneTip">没有数据啦~~</span>
            </yd-infinitescroll>
          </yd-layout>
        </el-col>
      </el-row>
    </div>
  </div>
</template>
<script>
import Item from '@/view/web/components/item'
import {dream} from '@/interface'
import ca1 from '@/assets/carousel1.jpg'
import ca2 from '@/assets/carousel2.jpg'
import receive from '@/assets/receive.png'
import noReceive from '@/assets/noReceive.png'
export default {
  data () {
    return {
      receive,
      noReceive,
      page: 1,
      page_size: 10,
      caArr: [ca1, ca2],
      searchValue: {
        is_claimed: '',
        title__icontains: ''
      },
      projectStatus: [
        {
          key: 111,
          value: '才能杀可能'
        }
      ],
      list: []
    }
  },
  mounted () {
    this.$axios.get(`/api/v1/monitor/dream/?page=${this.page}&page_size=${this.page_size}`).then(res => {
      this.list.push(...res.data.results)
    })
  },
  methods: {
    search () {
      this.page = 1
      this.getDate()
    },
    async loadList () {
      let results = await this.getDate()
      if (results.length < this.page_size) {
        this.$refs.dreamList.$emit('ydui.infinitescroll.loadedDone')
        return
      }
      this.$refs.dreamList.$emit('ydui.infinitescroll.finishLoad')
    },
    claim (id) {
      this.$router.push({path: `/index/${id}/details`, query: {id: id}})
    },
    async getDate () {
      let {data: {results}} = await dream({type: 'get', page: this.page, pageSize: this.page_size, search: this.searchValue})
      if (this.page === 1) {
        this.list = results
      } else {
        this.list.push(...results)
      }
      this.page++
      return results
    }
  },
  components: {
    Item
  }
}
</script>

<style>
.yd-flexview{
  max-height: 700px;
}
.main{
  padding-top: 20px
}
.breadcrumb{
  padding-bottom: 10px
}
.search-form{
  background: #f8f8f8;
  padding: 5px
}
.text-right{
  text-align: right
}
.el-form--inline .el-form-item{
  margin: 0
}
.el-carousel__item h3 {
  color: #475669;
  font-size: 14px;
  opacity: 0.75;
  line-height: 150px;
  text-align: center
}

.el-carousel__item:nth-child(2n) {
    background-color: #99a9bf;
}

.el-carousel__item:nth-child(2n+1) {
    background-color: #d3dce6;
}
</style>



