<template>
  <div style="background-color:#fff">
    <el-button-group>
      <el-button icon="el-icon-plus" @click="addUser">新增</el-button>
      <el-button>
        <el-upload
          action="/api/v1/monitor/upload_file/"
          :show-file-list="false"
          :headers="uploadHeaders"
          :limit="1"
          :on-success="handleFileSuccess"
          :name="'template'">
            <i class="el-icon-upload"></i> 导入
        </el-upload>
      </el-button>
      <el-button type="danger" :disabled="isDeteleAll" icon="el-icon-delete" @click="deteleAll">删除</el-button>
    </el-button-group>
    <el-table
      ref="multipleTable"
      :data="list"
      show-file-list="false"
      tooltip-effect="dark"
      style="width: 100%"
      @selection-change="handleSelectionChange">
      <el-table-column
        type="selection"
        width="55">
      </el-table-column>
      <el-table-column
        prop="title"
        label="标题">
      </el-table-column>
      <el-table-column
        prop="person_name"
        label="受助人">
      </el-table-column>
      <el-table-column
        prop="person_type"
        label="贫困程度"
        show-overflow-tooltip>
      </el-table-column>
      <el-table-column
        prop="want"
        label="理由"
        show-overflow-tooltip>
      </el-table-column>
      <el-table-column label="操作">
        <template slot-scope="scope">
          <el-button
            size="mini"
            type="primary"
            @click="handleView(scope.$index, scope.row)">查看</el-button>
          <el-button
            size="mini"
            type="primary"
            @click="handleEdit(scope.$index, scope.row)">修改</el-button>
          <el-button
            size="mini"
            type="danger"
            @click="handleDelete(scope.$index, scope.row)">删除</el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-pagination
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :page-sizes="[20, 50, 100, 200]"
      :page-size="page_size"
      layout="sizes, prev, pager, next"
      :total="count">
    </el-pagination>
    <!-- 新增修改 -->
    <el-dialog :title="title" :visible.sync="dialogFormVisible" width="30%">
      <el-form :model="group" ref="group" :rules="rules" :style="'width:400px;margin:0 auto'" label-width = "80px">
        <el-form-item label="标题" prop="title">
          <el-input :disabled="options === 'view' ? true : false" v-model="group.title"></el-input>
        </el-form-item>
        <el-form-item label="受助人" prop="person_name">
          <el-input :disabled="options === 'view' ? true : false" v-model="group.person_name"></el-input>
        </el-form-item>
        <el-form-item label="年龄" prop="age">
          <el-input :disabled="options === 'view' ? true : false" v-model="group.age"></el-input>
        </el-form-item>
        <el-form-item label="贫困程度" prop="person_type">
          <el-input :disabled="options === 'view' ? true : false" v-model="group.person_type"></el-input>
        </el-form-item>
        <el-form-item label="心愿" prop="want">
          <el-input :disabled="options === 'view' ? true : false" v-model="group.want"></el-input>
        </el-form-item>
        <el-form-item label="理由" prop="reason">
          <el-input :disabled="options === 'view' ? true : false" v-model="group.reason"></el-input>
        </el-form-item>
        <el-form-item label="地区" prop="local">
          <el-input :disabled="options === 'view' ? true : false" v-model="group.local"></el-input>
        </el-form-item>
        <el-form-item label="图片" prop="image_url">
          <el-upload
            class="avatar-uploader"
            action="/api/v1/monitor/upload_image/"
            :accept="'image/*'"
            :name="'image'"
            :headers="uploadHeaders"
            :show-file-list="false"
            :on-success="handleAvatarSuccess">
            <img v-if="image_url" :src="image_url" class="avatar">
            <i v-else class="el-icon-plus avatar-uploader-icon"></i>
          </el-upload>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="close">{{options === 'view' ? '关 闭' : '取 消'}}</el-button>
        <el-button type="primary" v-if="options === 'view' ? false : true" @click="submit('group')">确 定</el-button>
      </div>
    </el-dialog>
    <!-- 查看 -->
    <el-dialog :title="title" :visible.sync="dialogViewVisible" width="30%">
      <el-form :model="group" :rules="rules" :style="'width:400px;margin:0 auto'" label-width = "80px">
        <el-form-item label="标题:">
          <p>{{group.title}}</p>
        </el-form-item>
        <el-form-item label="受助人:">
          <p>{{group.person_name}}</p>
        </el-form-item>
        <el-form-item label="年龄">
          <p>{{group.age}}</p>
        </el-form-item>
        <el-form-item label="贫困程度:">
          <p>{{group.person_type}}</p>
        </el-form-item>
        <el-form-item label="心愿:">
          <p>{{group.want}}</p>
        </el-form-item>
        <el-form-item label="理由:">
          <p>{{group.reason}}</p>
        </el-form-item>
        <el-form-item label="地区:">
          <p>{{group.local}}</p>
        </el-form-item>
        <el-form-item label="图片:">
          <img v-if="group.image_url" :src="group.image_url" class="avatar">
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="close">关 闭</el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
import {dream} from '@/interface/admin'
import Cookie from 'js-cookie'
export default {
  data () {
    let age = (rule, value, callback) => {
      if (!/^\d+$/.test(value)) {
        callback(new Error('密码只能由大小写字母，数字和!@#$%^&*()_+组成，长度8到16位'))
      } else {
        callback()
      }
    }
    return {
      page: 1,
      page_size: 20,
      count: 20,
      list: [],
      multipleSelection: [],
      dialogFormVisible: false,
      dialogViewVisible: false,
      group: {
        title: '',
        person_name: '',
        age: '',
        person_type: '',
        want: '',
        reason: '',
        local: '',
        image_url: ''
      },
      image_url: '',
      rules: {
        title: [
          {required: true, message: '不能为空', trigger: 'blur'}
        ],
        person_name: [
          {required: true, message: '不能为空', trigger: 'blur'}
        ],
        age: [
          {required: true, message: '不能为空', trigger: 'blur'},
          {validator: age, message: '年龄只能为数字', trigger: 'blur'}
        ],
        person_type: [
          {required: true, message: '不能为空', trigger: 'blur'}
        ],
        want: [
          {required: true, message: '不能为空', trigger: 'blur'}
        ],
        reason: [
          {required: true, message: '不能为空', trigger: 'blur'}
        ],
        local: [
          {required: true, message: '不能为空', trigger: 'blur'}
        ],
        image_url: []
      },
      options: 'post',
      title: '新增梦想'
    }
  },
  computed: {
    uploadHeaders () {
      return {
        Authorization: `jwt ${Cookie.get('token')}`
      }
    },
    isDeteleAll () {
      return !this.multipleSelection.length
    }
  },
  mounted () {
    this.refresh()
  },
  methods: {
    async refresh () {
      let {data: {results, count}} = await dream({type: 'get', page: 1, pageSize: this.page_size})
      this.list = results
      this.count = count
    },
    async upfile () {
      console.log(Cookie.get('token'))
    },
    deteleAll () {
      let arr = []
      this.multipleSelection.map(row => {
        arr.push(dream({type: 'delete', ids: row.id}))
      })
      this.$alert(`批量删除`, '删除', {
        showCancelButton: true,
        cancelButtonText: '取消',
        confirmButtonText: '确定',
        confirmButtonClass: 'danger'
      }).then(async () => {
        this.$axios.all(arr).then(res => {
          this.refresh()
          this.$notify({
            title: '成功',
            message: '删除成功',
            type: 'success'
          })
        })
      }).catch(() => {
        this.$notify.error({
          title: '错误',
          message: '删除失败'
        })
      })
    },
    close () {
      this.dialogFormVisible = false
      this.dialogViewVisible = false
      this.clear()
    },
    clear () {
      Object.assign(this.group, {
        title: '',
        person_name: '',
        age: '',
        person_type: '',
        want: '',
        reason: '',
        local: '',
        contact_person: '',
        image_url: ''
      })
      this.image_url = ''
    },
    handleSizeChange (size) {
      this.page_size = size
      this.refresh()
    },
    handleCurrentChange (page) {
      this.page = page
      this.refresh()
    },
    handleSelectionChange (val) {
      this.multipleSelection = val
    },
    handleFileSuccess () {
      this.refresh()
    },
    handleAvatarSuccess (res, file) {
      this.image_url = URL.createObjectURL(file.raw)
      this.group.image_url = res.image_url
    },
    async handleEdit (index, row) {
      this.options = 'edit'
      this.title = '修改梦想'
      let {data} = await dream({type: 'get', ids: row.id})
      this.dialogFormVisible = true
      this.group = data
      this.image_url = data.image_url
    },
    async handleView (index, row) {
      this.options = 'view'
      this.title = '查看梦想'
      let {data} = await dream({type: 'get', ids: row.id})
      this.dialogViewVisible = true
      this.group = data
    },
    handleDelete (index, row) {
      this.$alert('删除', `删除${row.title}`, {
        showCancelButton: true,
        cancelButtonText: '取消',
        confirmButtonText: '确定',
        confirmButtonClass: 'danger'
      }).then(async () => {
        let {data} = await dream({type: 'delete', ids: row.id})
        if (!data) {
          this.refresh()
          this.$notify({
            title: '成功',
            message: '删除成功',
            type: 'success'
          })
          return
        }
        this.$notify.error({
          title: '错误',
          message: '删除失败'
        })
      }).catch(() => {})
    },
    addUser () {
      this.title = '新增梦想'
      this.dialogFormVisible = true
      this.options = 'add'
    },
    submit (ref) {
      this.$refs[ref].validate(async valid => {
        if (valid) {
          dream({type: 'post', form: this.group}).then(res => {
            this.clear()
            this.dialogFormVisible = false
            this.refresh()
            this.$notify({
              title: '成功',
              message: `${this.options === 'add' ? '新增' : '修改'}用户成功`,
              type: 'success'
            })
          })
        }
      })
    }
  }
}
</script>

<style>
.avatar-uploader .el-upload {
    border: 1px dashed #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
  }
  .avatar-uploader .el-upload:hover {
    border-color: #409EFF;
  }
  .avatar-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 178px;
    height: 178px;
    line-height: 178px;
    text-align: center;
  }
  .avatar {
    width: 178px;
    height: 178px;
    display: block;
  }
</style>



